const Preloader = () => {
  useEffect(() => {
    preLoaderAnim();
  });
  return (
    <div className="preloader">
      <div className="texts-container">
        <span>Developer, </span>
        <span>Curator, </span>
        <span>vibe. </span>
      </div>
    </div>
  );
};

export default Preloader;
